@import '../../../styles/variables.scss';

.profile {
  display: flex;
  width: 100%;

  &__content {
    width: 100%;
    text-align: center;
    margin: 60px auto 0;
  }
}

.profile__action {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__button {
    width: 500px;
    text-align: start;
    border-bottom: 1px solid $border-color-2;
    padding: 10px 0;

    &:last-child {
      border-bottom: none;
    }
  }

  &__homeLink {
    color: $error-color;

    &:hover {
      opacity: 0.7;
    }
  }
}
