@import '../../styles/variables.scss';

.login-form {
  margin: 135px auto 50px;
  width: 600px;
  min-height: 550px;
  box-shadow: $box-shadow;
  border-radius: 12px;
  padding: 30px 50px;
  color: $text-color;
  display: flex;
  flex-direction: column;
}

.login-form__header {
  margin: 0 auto;
  text-align: center;
  font-size: 1.25rem;
}

.login-form__content {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap: 10px;
}

.login-form__footer {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}
